    import {getCategoryDetail} from '@/api/imgAPI';
    import {uniSegmentedControl} from "@dcloudio/uni-ui";

    export default {
        components: {
            uniSegmentedControl
        },
        data() {
            return {
                items: [{title: "最新", order: "new"}, {title: "热门", order: "hot"}],
                current: 0,
                params: {
                    limit: 30,
                    skip: 0,
                    order: "new"
                },
                id: 0,
                // 页面显示数据的数组
                vertical: [],
                hasMore: true
            };
        },
        onLoad({id}) {
            this.id = id;
            this.getList(this.id);
        },
        methods: {
            onClickItem(e) {
                /*
                1 根据被点击的标题 切换标题
                2 切换order
                3 重新发送请求
                 */

                if (this.current !== e.currentIndex) {
                    this.current = e.currentIndex;
                } else {
                    // 点击的是相同的标题
                    return;
                }
                this.params.order = this.items[e.currentIndex].order;
                this.params.skip = 0;
                this.vertical = [];
                this.getList(this.id);
            },
            getList(id) {
                getCategoryDetail({...this.params, id}).then(result => {
					console.log(result);
                    let vertical = result.data.res.vertical;
                    // console.log(result);
                    if (vertical.length === 0) {
                        this.hasMore = false;
                        uni.showToast({
                            title: "没有更多数据了",
                            icon: "none"
                        });
                        return;
                    }
                    this.vertical = [...this.vertical, ...vertical];
                })
            },
            // 加载下一页数据
            handleScrolltolower() {
                if (this.hasMore) {
                    this.params.skip += this.params.limit;
                    this.getList(this.id);
                } else {
                    uni.showToast({
                        title: "没有更多数据了",
                        icon: "none"
                    })
                }
            }
        }
    };
